@use "global" as *;
.blog {
  @include mq(md) {
  }
}

.blog__title {
  display: block;
  text-align: center;
  font-family: $base-font-family;
  font-style: normal;
  font-weight: $normal;
  font-size: rem(20);
  line-height: 1.45;
  letter-spacing: rem(2);
  position: relative;
  @include mq(md) {
  }
}

.blog__title::before {
  content: "Blog";
  display: block;
  font-family: $second-font-family;
  font-style: normal;
  font-weight: $normal;
  font-size: rem(50);
  line-height: 1.44;
  letter-spacing: rem(2);
  color: rgba(249, 248, 113, 0.2);
  position: absolute;
  top: rem(-44);
  left: 50%;
  transform: translateX(-50%);
  @include mq(md) {
  }
}

.blog__title-layout {
  margin-bottom: rem(72);
  @include mq(md) {
  }
}

.blog__layout {
  padding: 0 rem(20);
  margin-bottom: rem(120);
  @include mq(md) {
  }
}

.blog__col3 > .card__layout:nth-of-type(3) {
  margin-bottom: rem(0);
  @include mq(md) {
  }
}

.blog__col3-layout {
  margin-bottom: rem(24);
  @include mq(md) {
  }
}
